body {
    margin: 0;
    padding: 0;
    background-color: white;
    color: rgb(60, 158, 143);
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none
}

.wrapper {
    color: rgb(60, 158, 143);
}

.wrapper ul li {
    padding-top: 5px;
}

#nav {
    display: flex;
    justify-content: center;
    font-size: 36px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-bottom: 2px solid;
    color: #4ABDAC;
    margin: 0;
    padding: 0;
    text-decoration: none;
}


#nav #nav-links li {
    list-style: none;
    text-align: center;
    border-radius: 10px;
    display: inline;
    padding: 10px;
    color: #4ABDAC
}

@media only screen and (max-width: 768px) {

    #nav {
        justify-content: center;
    }
    #nav #nav-links li {
        flex-wrap: wrap;
        font-size: 20px;
        padding: 10px;
        margin: 0px;
    }

}


a {
    padding: 10px;
    text-decoration: none;
    color: rgb(60, 158, 143)
}



.active {
    background-color: #4ABDAC;
    border-radius: 5px;
    color:white;
    border-bottom: 6px solid rgb(39, 99, 90);
    transition: 0.4s;
}

a:active {
    color: #4ABDAC;
}

h1 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

p {
    font-size: 18px;
}



.fade-enter {
    opacity: 0.1
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }

.fade-exit {
    opacity: 1;
  }

.fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 250ms ease-in;
  }

div.transition-group {
    position: relative;
  }

section.route-section {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }

#home-background {
    background: linear-gradient(rgba(74, 189, 172, 0.8), rgba(74, 189, 172, 0.8)), url('https://images.pexels.com/photos/251225/pexels-photo-251225.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;
    text-decoration: none;
}

h1 {
    font-weight: 650;
    font-size: 72px;
    color: rgb(255, 255, 255);
    margin-bottom: 10px;
}

h3 {
    font-weight: 600;
    font-size: 42px;
    color: rgba(255, 255, 255);
    margin-top: 0;
}

#subtitle {
    text-decoration: none;
}

h4 {
    font-weight: 550;
    font-size: 24px;
    color: rgba(255, 255, 255);
    margin-top: 10;
    padding-bottom: 50px;
}

@media only screen and (max-width: 500px) {

    h1 {
        font-size: 32px;
        padding-bottom: 20px
    }

    h3 {
        font-size: 20px;
    }

    h4 {
        font-size: 16px;
    }
}
    


li {
    line-height: 2
}

.welcome-box {
    display: flex;
    justify-content: center;
}

.welcome-box-item {
    padding: 80px 20px;
}

.main-image {
    border-radius: 50%;
    max-height: 120px;
    max-width: 120px;
    margin-top: 75px;
}

.welcome {
    margin-left: 0;
    margin-top: 10px;
}

#about-background {
    background: linear-gradient(rgba(74, 189, 172, 0.8), rgba(74, 189, 172, 0.8)), url('https://images.pexels.com/photos/159621/open-book-library-education-read-159621.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
    background-size: auto;
    background-position: center;
    height: 100%;
    width: 100%;
    color: white;
    font-weight: 550;
    margin: 0;
    padding: 0;
}

h3 {
    text-decoration: underline;
    margin: 10px;
}

.about-me {
    margin-right: 15px;
}

i {
    font-size: 4rem;
    padding: 10px;
}

.skills {
    display : flex;
    flex-flow: wrap;
    padding: 10px;
    margin: 15px 0px;
    justify-content: center
}

.grid-container {
    display: grid;
    grid-column-gap: 10px;
    color: white;
}

.grid-item {
    justify-content: center;
    margin-left: 1rem;
    color: white;
    text-align: center;
}

#download-cv {
    border: 1px solid white;
    justify-content: center;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 10px auto;
    border-radius: 20px;
    padding: 15px;
    color: white;
    background-color: rgba(50, 124, 113, 0.8)
}

#download-cv:hover {
    background-color: rgba(62, 153, 139, 0.8)
}


@media only screen and (max-width: 768px) {


    .grid-container {
        display: block;
        margin: 0;
    }

    .grid-item {
        margin: 0;

    }

    h3 {
        margin-top: -10px;
        padding: 0;
    }

}

.info {
    margin-top: 10px;
    margin-left: 20px;
    line-height: 2;
}

.info p {
