}
#projects-background {
    background: linear-gradient(rgba(74, 189, 172, 0.8), rgba(74, 189, 172, 0.8)), url('https://images.pexels.com/photos/7096/people-woman-coffee-meeting.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
