}


.contact-flex-container {
    display: flex;
    margin: 55px;
    flex-flow: row wrap;
    justify-content: center;
    grid-template-columns: 1fr 1fr;
}

.contact-flex-item {
    text-align: center;
    max-width: 300px;
    max-height: 500px;
    text-decoration: none;
}

.contact-flex-item a {
    text-align: center;
    text-decoration: none;
    color: white;
}

.contact-flex-container img {
    max-height: 80px;
    max-width: 80px;
    padding: 20px;
}

#contact-title {
    text-align: center;
}

h2 {
    margin: 0;
    padding: 30px;
    color: white;
}

a:visited {
    text-decoration: none;
}

.contacts-flex-item a:visited {
    text-decoration: none;
    color: white;
}

h3:visited {
    text-decoration: none;
    color: white;
}

/* Form */
.contact-form{
	max-width: 400px;
	padding: 10px 20px;
	background: #f4f7f8;
	margin: 10px auto;
	padding: 20px;
	background: #f4f7f8;
	border-radius: 8px;
}

.contact-form legend {
	font-size: 1.4em;
	margin-bottom: 10px;
}

.contact-form input[type="text"],
.contact-form input[type="email"],
.contact-form textarea,
.contact-form select {
	background: rgba(255,255,255,.1);
	border: none;
	border-radius: 4px;
	font-size: 15px;
	margin: 0;
	outline: 0;
	padding: 10px;
    width: 100%;
    max-width: inherit;
	box-sizing: border-box; 
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box; 
	background-color: #e8eeef;
	color:#8a97a0;
	box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
	margin-bottom: 30px;
}

.contact-form input[type="text"]:focus,
.contact-form input[type="email"]:focus,
.contact-form textarea:focus,
.contact-form select:focus{
	background: #d2d9dd;
}
.contact-form select{
	-webkit-appearance: menulist-button;
	height:35px;
}

.contact-form button
{
    position: relative;
    cursor: pointer;
	display: block;
	padding: 10px 20px 10px 20px;
	color: #FFF;
	margin: 0 auto;
	background: #1abc9c;
	font-size: 14px;
	text-align: center;
	font-style: normal;
	border: 1px solid #16a085;
	border-width: 1px 1px 3px;
	margin-bottom: 10px;
}

.contact-form button:hover
{
	background: #109177;
}

.divider {
    border-bottom: 2px solid white;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
}

.social {
    margin: 0 auto;
    text-align: center;
}

.social img {
    padding: 5px 10px;
}
