#about-background {
    background: linear-gradient(rgba(74, 189, 172, 0.8), rgba(74, 189, 172, 0.8)), url('https://images.pexels.com/photos/159621/open-book-library-education-read-159621.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
    background-size: auto;
    background-position: center;
    height: 100%;
    width: 100%;
    color: white;
    font-weight: 550;
    margin: 0;
    padding: 0;
}

h3 {
    text-decoration: underline;
    margin: 10px;
}

.about-me {
    margin-right: 15px;
}

i {
    font-size: 4rem;
    padding: 10px;
}

.skills {
    display : flex;
    flex-flow: wrap;
    padding: 10px;
    margin: 15px 0px;
    justify-content: center
}

.grid-container {
    display: grid;
    grid-column-gap: 10px;
    color: white;
}

.grid-item {
    justify-content: center;
    margin-left: 1rem;
    color: white;
    text-align: center;
}

#download-cv {
    border: 1px solid white;
    justify-content: center;
    text-align: center;
    width: fit-content;
    margin: 10px auto;
    border-radius: 20px;
    padding: 15px;
    color: white;
    background-color: rgba(50, 124, 113, 0.8)
}

#download-cv:hover {
    background-color: rgba(62, 153, 139, 0.8)
}


@media only screen and (max-width: 768px) {


    .grid-container {
        display: block;
        margin: 0;
    }

    .grid-item {
        margin: 0;

    }

    h3 {
        margin-top: -10px;
        padding: 0;
    }

}

.info {
    margin-top: 10px;
    margin-left: 20px;
    line-height: 2;
}

.info p {
    color: white;
}

.past {
    margin-top: 10px;
    margin-left: 20px;
}

.about-links {
    color: white
}